body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

html {
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  height: 100%;
}

body {
  height: 100%;
}

.App {
  text-align: center;
  background-color: darkgray;
  height: 100%;
  /* min-height: 950px; */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  background-color: darkblue;
}

.header-title {
  display: flex;
  margin-left: 15px;
  color: white;
  font-weight: bold;
  font-size: 32px;
  height: 60px;
  padding-top: 10px;
}

.header-links {
  margin-right: 15px;
}

.header-link {
  list-style-type: none;
  color: green;
  font-weight: bold;
} 

.footer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 20px;
  background-color: darkblue;
  width: 100%;
  position: absolute;
  bottom: 0;
  
}

.footer-title {
  color: white;
}

.signup-field {
  /* margin-bottom: 15px; */
  background-color: white;
  height: 30px;
  padding-left: 10px;
  border-radius: 5px;
}

.signup-form {
  background-color: lightgray;
  width: 400px;
  padding: 30px;
  border: 1px solid black;
  /* margin-top: 20px; */
  /* margin-left: 35%; */
  /* margin-bottom: 20px; */
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 5px;
}

.signup-title {
  font-size: 24px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 20px;
}

.signup-body {
  display: flex;
  flex-direction: column;
  justify-content: left;
}

.signup-names-fields {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.signup-button:hover {
  cursor: pointer;
}

#first-name,
#last-name {
  width: 180px;
}

.signup-button {
  width: 400px;
  background-color: green;
  color: white;
  border-radius: 5px;
}

.login-portal {
  margin-top: 15px;
}

.login-form {
  background-color: lightgray;
  height: 300px;
  width: 300px;
  position: fixed;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 5px;
  padding: 10px;
  border: 1px black solid;
}

.login-row {
  margin: 20px;
}

.login-field {
  width: 200px;
  height: 30px;
  padding-left: 5px;
}

.login-button {
  width: 200px;
  height: 30px;
  background-color: green;
  color: white;
}

.login-form-title {
  font-weight: bold;
  font-size: 24px;
  margin-bottom: 40px;
  margin-top: 30px;
}

.dashboard-container {
  width: 80%;
  background-color: white;
  margin-left: 30px;
  margin-top: 30px;
}

.dashboard-title {
  text-align: left;
  color: white;
  font-weight: bold;
  background-color: darkblue;
  height: 50px;
  font-size: 20px;
  padding-left: 10px;
  padding-top: 5px;
}

.dashboard-body {
  height: 300px;
  background-color: white;
}

button {
  width: 100px;
  height: 35px;
  font-weight: bold;
}

button:hover {
  cursor: pointer;
}

.container {
  width: 100%;
  display: flex;
  /* flex-direction: row; */
  margin-top: 30px;
}

.game-container {
  width: 750px;
  margin-left: 30px;
}

.bet-slip {
  width: 345px;
  /* margin-top: 30px; */
  margin-left: 100px;
}

.table {
  border: none;
  background-color: lightgray;
  /* padding: 5px; */
  width: 100%;
}

.render-games {
  margin-bottom: 20px;
  margin-left: 30px;
  padding: 10px;
  background-color: white;
  /* display: flex; */
  /* flex-direction: column; */
  /* flex-wrap: wrap; */
  border: none
}

.render-main-body {
  background-color: white;
  margin-bottom: 10px;
  width: 750px;
  padding: 10px;
  height: 100px;
  border-radius: 5px;
  font-size: 13px;
}


/* .render-games {
  background-color: white;
  margin-bottom: 50px;
} */

.render-game {
  display: flex;
  flex-direction: row;
  margin: 10px;
  flex-wrap: wrap;
}

.render-team {
  /* margin-right: 10px; */
  /* margin-left: 10px; */
  text-align: left;
  font-weight: bold;
  width: 250px;
}

.render-sport-title {
  font-size: 20px;
  background-color: darkblue;
  color: white;
  /* margin-left: 30px; */
  font-weight: bold;
  height: 50px;
  text-align: left;
  padding-left: 10px;
  padding-top: 5px;
}

.render-gametime {
  margin-left: 10px;
  font-weight: bold;
}

.render-away-row {
  height: 10px;
}

.render-sub-body {
  margin-left: 150px;
  /* padding: 10px; */
  height: 10px;
}


.render-button {
  /* padding-left: 10px;
  padding-right: 10px; */
  width: 120px;
}

#header-time {
  width: 100px;
  padding-left: 20px;
  text-align: left;
}

#header-team {
  text-align: left;
  padding-left: 40px;
}

#header-money {
  text-align: left;
  padding-left: 220px;
}

#header-spread {
  text-align: left;
  padding-left: 15px;

}

#header-total {
  padding-left: 60px;
}

.tr {
  text-align: left;
}

.table-headers {
  padding: 20px;
}

.render-gametime-row {
  padding: none;
}

.scroll-container {
  /* padding-left: 10px;
  padding-right: 10px; */
  height: 375px;
  overflow: scroll;
  overflow-x: hidden;
}

.slip-title {
  background-color: darkblue;
  color: white;
  height: 50px;
  display: flex;
  flex-direction: row;
  /* justify-content: center; */
  font-weight: bold;
  font-size: 20px;
  padding-left: 10px;
  padding-top: 5px;
}

.slip-body {
  background-color: white;
  padding: 10px;
}

.win-value {
  color: green;
}

.lose-value {
  color: red;
}

.slip-exit {
  /* display: flex;
  flex-direction: row; */
  text-align: right;
  /* justify-content: right; */
}

.slip-exit:hover {
  cursor: pointer;
}

.slip-buttons {
  margin: 10px;
}

.slip-button {
  margin: 10px;
  font-weight: bold;
}

#submit-slip {
  background-color: green;
  color: white;
}

.slip-to-lose {
  height: 20px;
  width: 50px;
}

.slip-away-team {
  text-align: left;
}

.slip-home-team {
  text-align: left;
}

.slip-money {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin: 20px;
}

.slip-bet-info {
  margin-top: 10px;
  text-align: left;
}

.members-page {
  padding-left: 50px;
  padding-right: 50px;
  margin-left: 200px;
  margin-bottom: 20px;
}

body {
  height: 100%;
}
